import React, { useState } from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import carSrc from "./car.png";
import carBlackSrc from "./carblack.png";
import tyreSrc from "./tyre.png";
import engineSrc from "./engine.png";
import doorSrc from "./door.png";
import bonetSrc from "./bonet.png";
import windowsSrc from "./windows.png";
import './car.css'
import { Questions } from './parts/questions';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);

const colors = ['green', 'purple', 'blue', 'red'];

const labelsConst = ['Option 1', 'Option 2', 'Option 3', 'Option 4'];

const defaultCo2Data = {
    labels: labelsConst,
    datasets: [
        {
            label: 'CO2 Data',
            data: [],
            backgroundColor: colors,
        }
    ],
};

const defaultEnergyData = {
    labels: labelsConst,
    datasets: [
        {
            label: 'Energy Data',
            data: [],
            backgroundColor: colors,
        }
    ],
};

const options = {
    responsive: true,
    plugins: {
        legend: {
            display: false
        },
        title: {
            display: true,
            text: 'CO2 Data',
        },
    },
};

const optionsEnergy = {
    responsive: true,
    plugins: {
        legend: {
            display: false
        },
        title: {
            display: true,
            text: 'Energy Data',
        },
    },
};

export function CarModel(props) {
    const [selectSrc, setSelectedSrc] = useState(bonetSrc);
    const [selectedPart, setSelectedPart] = useState('Bonnet');
    const [submittedOptions, setSubmittedOptions] = useState([]);
    const [chartData, setChartData] = useState(defaultCo2Data);

    const [energyData, setEnergyData] = useState(defaultEnergyData);
    let cor = '';

    const onImageClick = (e) => {
        // cor = cor + `,${e.clientX - 41},${e.clientY - 103}`;
        // console.log(cor);
        // console.log('image')
    }
    // const onCarClick = (e) => {
    //     console.log("car click");
    // }

    // const onWindowClick = (e) => {
    //     setSelectedSrc(windowsSrc);
    // }

    const onEngineClick = (e) => {
        setSelectedPart('Engine');
        setSubmittedOptions([]);
        setChartData(defaultCo2Data);
        setEnergyData(defaultEnergyData);
        setSelectedSrc(engineSrc);
    }

    const onBonetClick = (e) => {
        setSelectedPart('Bonnet');
        setSubmittedOptions([]);
        setChartData(defaultCo2Data);
        setEnergyData(defaultEnergyData);
        setSelectedSrc(bonetSrc);
    }

    const onTyreClick = () => {
        setSelectedPart('Tyre');
        setSubmittedOptions([]);
        setChartData(defaultCo2Data);
        setEnergyData(defaultEnergyData);
        setSelectedSrc(tyreSrc);
    }

    const onDoorClick = () => {
        setSelectedPart('Door');
        setSubmittedOptions([]);
        setChartData(defaultCo2Data);
        setEnergyData(defaultEnergyData);
        setSelectedSrc(doorSrc);
    }

    const onSubmitAnswers = (data) => {
        let weightMultiplyFactor = selectSrc == bonetSrc ? 3.5 : (selectSrc == doorSrc) ? 2.5 : 1;
        let tempData = [...submittedOptions];
        if (tempData.length > 3) tempData.shift();
        tempData.push({
            totalCO2: data.totalCO2,
            fibre: data.fibre,
            resin: data.resin,
            process: data.process,
            RW: data.rw,
            totalEnergy: data.totalEnergy,
        })
        setSubmittedOptions(tempData);

        const tempLabels = labelsConst.slice(0, tempData.length)

        const tempCO2ChartData = {
            labels: tempLabels,
            datasets: [
                {
                    label: 'CO2 Data',
                    data: tempData.map(d => weightMultiplyFactor * d.totalCO2),
                    backgroundColor: colors.slice(0, tempData.length),
                }
            ],
        };

        const tempEnergyChartData = {
            labels: tempLabels,
            datasets: [
                {
                    label: 'Energy Data',
                    data: tempData.map(d => weightMultiplyFactor * d.totalEnergy),
                    backgroundColor: colors.slice(0, tempData.length),
                }
            ],
        };
        console.log(tempEnergyChartData)
        setEnergyData(tempEnergyChartData);
        setChartData(tempCO2ChartData)
    }

    return (
        <>
            <div style={{ height: '50px', width: '100%', backgroundColor: 'black' }}></div>
            <div className="container" style={{ padding: '0px 0px ', marginTop: '20px' }}>
                <div className="row">
                    <div className="col-xl-6" style={{ border: 'solid', borderWidth: '0.01rem', borderColor: 'black' }}>
                        <div className='center' style={{ position: 'relative' }}>
                            <img src={carBlackSrc} usemap="#workmap" style={{ width: '600px', position: 'relative', borderWidth: '0.1rem', borderColor: 'black' }} />
                            <img src={selectSrc} usemap="#workmap2" style={{ width: '600px', position: 'absolute', borderWidth: '0.1rem', borderColor: 'black' }} />
                            <map name="workmap">
                                <area shape="poly" coords="67,182,74,191,85,199,106,204,121,218,139,223,158,229,182,215,216,204,250,193,270,186,280,181,262,175,240,168,214,153,193,144,170,129,164,122,149,125,123,137,98,149,82,164,72,171"
                                    alt="engine" onClick={onEngineClick} />
                                <area shape="poly" coords="200,276,211,264,233,248,251,245,265,254,274,267,278,283,274,304,264,326,247,339,231,343,216,342,197,334,189,320,185,299,190,287"
                                    alt="tyre" onClick={onTyreClick} />
                                <area shape="poly" coords="304,189,321,184,319,171,322,154,330,125,333,114,336,104,336,101,348,97,373,90,396,83,403,83,407,93,409,114,409,131,410,139,415,145,423,159,426,169,427,180,420,195,416,211,413,221,409,230,388,239,363,252,336,259,315,266,297,270,305,238,306,221,306,208,304,195"
                                    alt="door" onClick={onDoorClick} />
                                <area shape="poly" coords="170,119,181,119,196,123,220,135,242,148,258,160,275,169,268,159,257,147,246,135,222,117,201,101,184,89,160,74,142,66,132,66,116,63,136,82,150,96,164,113"
                                    alt="bonet" onClick={onBonetClick} />

                            </map>
                            <map name="workmap2">
                                <area shape="poly" coords="67,182,74,191,85,199,106,204,121,218,139,223,158,229,182,215,216,204,250,193,270,186,280,181,262,175,240,168,214,153,193,144,170,129,164,122,149,125,123,137,98,149,82,164,72,171"
                                    alt="engine" onClick={onEngineClick} />
                                <area shape="poly" coords="200,276,211,264,233,248,251,245,265,254,274,267,278,283,274,304,264,326,247,339,231,343,216,342,197,334,189,320,185,299,190,287"
                                    alt="tyre" onClick={onTyreClick} />
                                <area shape="poly" coords="304,189,321,184,319,171,322,154,330,125,333,114,336,104,336,101,348,97,373,90,396,83,403,83,407,93,409,114,409,131,410,139,415,145,423,159,426,169,427,180,420,195,416,211,413,221,409,230,388,239,363,252,336,259,315,266,297,270,305,238,306,221,306,208,304,195"
                                    alt="door" onClick={onDoorClick} />
                                <area shape="poly" coords="170,119,181,119,196,123,220,135,242,148,258,160,275,169,268,159,257,147,246,135,222,117,201,101,184,89,160,74,142,66,132,66,116,63,136,82,150,96,164,113"
                                    alt="bonet" onClick={onBonetClick} />
                            </map>
                        </div>
                        <Questions onSubmit={onSubmitAnswers} selectedPart={selectedPart} />
                    </div>
                    <div className="col-lg-6" style={{ backgroundColor: '#DCDCDC', position: 'relative' }}>
                        <table style={{ marginTop: '15px' }}>
                            <tr>
                                <th className='tabelWidth'>Fibre</th>
                                <th className='tabelWidth'>Resin</th>
                                <th style={{ width: '90px' }}>Fibre%</th>
                                <th style={{ width: '210px' }}>Process</th>
                                <th style={{ width: '90px' }}>Color</th>
                            </tr>
                            {
                                submittedOptions.map((option, i) =>
                                    <tr key={i}>
                                        <td>{option.fibre}</td>
                                        <td>{option.resin}</td>
                                        <td>{option.RW} %</td>
                                        <td>{option.process}</td>
                                        <td ><div style={{ width: '50px', height: '20px', backgroundColor: colors[i] }}></div></td>
                                    </tr>
                                )
                            }
                        </table>
                        <div style={{ marginTop: '15px' }}>
                            <Bar data={chartData} options={options} height={90} />
                            <Bar data={energyData} options={optionsEnergy} height={90} />
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}


