import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { CarModel } from "./pages/car/main";
import { SolarPVCircuitMainPage } from "./pages/circuit/mainPage";
import { BatteryCircuitMainPage } from "./pages/circuit2/mainPage";
import { HydroMainPage } from "./pages/hydro/mainPage";
import { MainPage } from "./pages/main";
import { SolarMainPage } from "./pages/solar/mainPage";
import { SolarHeaterMainPage } from "./pages/solarheat/mainPage";

export function App(props) {
  return (
    <Router>
      <Routes>
        {/* <Route path="/" exact element={<MainPage />} /> */}
        <Route path="/circuit2" exact element={<BatteryCircuitMainPage />} />
        <Route path="/" exact element={<CarModel />} />
        <Route path="/circuit" exact element={<SolarPVCircuitMainPage />} />
        <Route path="/solar2" exact element={<SolarHeaterMainPage />} />
        <Route path="/hydro" exact element={<HydroMainPage />} />
        <Route path="/solar" exact element={<SolarMainPage />} />
      </Routes>
    </Router>
  );
}
