import React from "react";
import SolarPVSrc from "../assets/main.jpg";

export function SolarPVDetails(props) {
  return (
    <div>
      <div
        style={{
          width: "1020px",
          position: "absolute",
          border: "solid",
          borderWidth: "0.1rem",
          borderColor: "white",
          left: "10px",
          bottom: "10px",
        }}
      >
        <img src={SolarPVSrc} style={{ width: "100%" }} />
        <div
          style={{
            position: "absolute",
            top: "5%",
            left: "10%",
            border: "solid",
            borderWidth: "3px",
            borderRadius: "10px",
            borderColor: "white",
            width: "80%",
            height: "90%",
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              opacity: "0.85",
              position: "absolute",
            }}
          >
            <div style={{ padding: "0px 20px", marginTop: "20px" }}>
              <h2>Theory</h2>
              <p
                style={{
                  fontFamily: "Arial",
                  fontSize: "19px",
                  marginTop: "20px",
                  display: "block",
                  lineHeight: "20px",
                }}
              >
                Demonstrate the number of batteries in series and parallel to
                get the given Voltage, Power and Energy requirements
              </p>
              {/* <h2>Lab Goals</h2> */}
            </div>
          </div>
          <div style={{ position: "absolute", left: "42%", bottom: "20px" }}>
            <button
              onClick={() => props.onStepChange(3)}
              className="button-solarPV"
              role="button"
            >
              <i className="fa fa-play" style={{ marginRight: "5px" }}></i> Next
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
