import React, { useState } from 'react';
import { Slider } from '@material-ui/core';
import Typography from '@mui/material/Typography';

export const fibreMaterials = [
    { name: 'Carbon Fibre', value: 0, co2: 48.99, weight: 1, energy: 1040.87 },
    { name: 'Glass Fibre ', value: 1, co2: 2.16, weight: 1.4, energy: 24.48 },
    { name: 'Flax Fibre', value: 2, co2: 0.482, weight: 2, energy: 13.67 },
]
export const resinMaterials = [
    { name: 'Epoxy Resin', value: 0, co2: 6.75, energy: 135.04},
    { name: 'Biobased Epoxy', value: 1, co2: 4.54, energy:91},
    { name: 'PFA', value: 2, co2: 0.198, energy:173.34},
]

export const manufacturingProcess = [
    { name: 'Resin Infusion', value: 0, co2: 1.25, energy:18.41},
    { name: 'Carbon Fibre/Epoxy Prepreg', value: 1, co2: 2.37, energy:40},
    { name: 'SMC Compression Moulding/Press', value: 2, co2: 1.59, energy: 26.45},
]

export function Questions(props) {
    const [selectedFibre, setSelectedFibre] = useState(0);
    const [selectedResin, setSelectedResin] = useState(0);
    const [selectedManuProcess, setSelectedManuProcess] = useState(0);
    const [rwvalue, setRWValue] = useState(50);

    const changeRWValue = (event, value) => {
        setRWValue(value);
    }
    const getText = (valu) => `${rwvalue}%`;

    const onsubmitClick = () => {
        const perKgCo2 = fibreMaterials[selectedFibre].co2 * (1 - rwvalue / 100) +
            resinMaterials[selectedResin].co2 * (rwvalue / 100) +
            manufacturingProcess[selectedManuProcess].co2;
        const co2 = parseFloat((perKgCo2  * fibreMaterials[selectedFibre].weight).toFixed(2));

        const perKgEnergy = fibreMaterials[selectedFibre].energy * (1 - rwvalue / 100) +
            resinMaterials[selectedResin].energy * (rwvalue / 100) +
            manufacturingProcess[selectedManuProcess].energy;
        const energy = parseFloat((perKgEnergy  * fibreMaterials[selectedFibre].weight).toFixed(2));

        const tempData= {
            totalCO2: co2,
            fibre: fibreMaterials[selectedFibre].name,
            resin: resinMaterials[selectedResin].name,
            process: manufacturingProcess[selectedManuProcess].name,
            rw: rwvalue,
            totalEnergy:energy,
        }
        props.onSubmit(tempData)
    }

    return (
        <>
            <div style={{ marginTop: '0px' }}>
                <h5>Please select materials for {props.selectedPart}</h5>
                <div style={{ marginTop: '10px' }}>
                    <label for="cars">Choose Fibre : </label>
                    <select value={selectedFibre} onChange={(e) => setSelectedFibre(e.target.value)} style={{ marginLeft: '5px' }}>
                        {fibreMaterials.map((fibreMaterial) =>
                            <option value={fibreMaterial.value}>{fibreMaterial.name}</option>
                        )}
                    </select>
                </div>
                <div style={{ marginTop: '10px' }}>
                    <label for="cars">Choose Resin : </label>
                    <select value={selectedResin} onChange={(e) => setSelectedResin(e.target.value)} style={{ marginLeft: '5px' }}>
                        {resinMaterials.map((resin) =>
                            <option value={resin.value}>{resin.name}</option>
                        )}
                    </select>
                </div>

                <div style={{ marginTop: '10px' }}>
                    <label for="cars">Choose Manufacturing Process : </label>
                    <select value={selectedManuProcess} onChange={(e) => setSelectedManuProcess(e.target.value)} style={{ marginLeft: '5px' }}>
                        {manufacturingProcess.map((process) =>
                            <option value={process.value}>{process.name}</option>
                        )}
                    </select>
                </div>

                <div style={{ marginTop: '15px' }}>
                    <Typography id="non-linear-slider" gutterBottom>
                        RW: {rwvalue}%
                    </Typography>
                    <Slider
                        size="small"
                        style={{ width: 300 }}
                        min={0}
                        max={100}
                        step={1}
                        value={rwvalue}
                        onChange={changeRWValue}
                        valueLabelDisplay="on"
                        getAriaValueText={getText}
                        valueLabelFormat={getText}
                        aria-labelledby="discrete-slider-restrict"
                        // marks={marks}
                        aria-label="Small"
                    // color="secondary"
                    />
                    {rwvalue}
                </div>
                <div style={{ marginTop: '10px', marginBottom: '20px' }}>
                    <button className='btn btn-primary' onClick={onsubmitClick}>Submit</button>
                </div>
            </div>
        </>
    );
}


