import React, { useEffect, useState } from 'react';
import '../circuit.css';
import Draggable from 'react-draggable';
import batterySrc from '../assets/battery.png'
import sunSrc from '../assets/sun.png'
import raysSrc from '../assets/rays.PNG'
import cflSrc from '../assets/cfl.png'
import ledSrc from '../assets/led.png'
import solarSrc from '../assets/solar.png'
import controllerSrc from '../assets/controller.png'
import inverterSrc from '../assets/inverter.jpg'
import dcSrc from '../assets/dcpanel.png'
import acSrc from '../assets/acBoard.png'
import tvSrc from '../assets/tv.png'
import fanSrc from '../assets/fan.png'

const sunPosition = {
    top:65,
    left:100
}

export function Practical(props) {
    const [controller, setController] = useState({ x: -1, y: -1, show: false, correctPoint: false });
    const [battery, setBattery] = useState({ x: -1, y: -1, show: false, correctPoint: false });
    const [inverter, setInverter] = useState({ x: -1, y: -1, show: false, correctPoint: false });
    const [led, setLED] = useState({ x: -1, y: -1, show: false, correctPoint: '' });
    const [cfl, setCFL] = useState({ x: -1, y: -1, show: false, correctPoint: '' });
    const [ac, setAC] = useState({ x: -1, y: -1, show: false, correctPoint: false });
    const [dc, setDC] = useState({ x: -1, y: -1, show: false, correctPoint: false });
    const [tv, setTV] = useState({ x: -1, y: -1, show: false, correctPoint: '' });
    const [fan, setFan] = useState({ x: -1, y: -1, show: false, correctPoint: '' });

    const checkWhichPoint = (x, y, point) => {
        // console.log(x,y,point);
        if ((13 <= y && y <= 92) && (513 <= x && x <= 610)) {
            // Point A
            if (point == 'A') {
                setController({ ...controller, x: 565, y: 50, correctPoint: true });
            } else {
                return false;
            }
        }
        else if ((166 <= y && y <= 232) && (513 <= x && x <= 610)) {
            if (point == 'B') {
                setBattery({ ...battery, x: 565, y: 190, correctPoint: true });
            } else {
                return false;
            }
        }
        else if ((302 <= y && y <= 398) && (530 <= x && x <= 612)) {
            if (point == 'C') {
                setInverter({ ...inverter, x: 573, y: 348, correctPoint: true });
            } else {
                return false;
            }
        }
        else if ((139 <= y && y <= 236) && (346 <= x && x <= 446)) {
            if (point == 'D') {
                setDC({ ...dc, x: 400, y: 180, correctPoint: true });
            } else {
                return false;
            }
        }
        else if ((296 <= y && y <= 392) && (360 <= x && x <= 447)) {
            if (point == 'G') {
                setAC({ ...ac, x: 400, y: 345, correctPoint: true });
            } else {
                return false;
            }
        }
        else if ((136 <= y && y <= 238) && (237 <= x && x <= 297)) {
            if (point == 'E') {
                setLED({ ...led, x: 270, y: 188, correctPoint: 'E' });
            } else if (point == 'F') {
                setCFL({ ...cfl, x: 270, y: 188, correctPoint: 'E' });
            } else {
                return false;
            }
        }
        else if ((136 <= y && y <= 238) && (82 <= x && x <= 141)) {
            if (point == 'E') {
                setLED({ ...led, x: 110, y: 188, correctPoint: 'F' });
            } else if (point == 'F') {
                setCFL({ ...cfl, x: 110, y: 188, correctPoint: 'F' });
            } else {
                return false;
            }
        }
        else if ((295 <= y && y <= 402) && (35 <= x && x <= 138)) {
            if (point == 'H') {
                setFan({ ...fan, x: 98, y: 346, correctPoint: 'I' });
            } else if (point == 'I') {
                setTV({ ...tv, x: 85, y: 358, correctPoint: 'I' });
            } else {
                return false;
            }
        }
        else if ((295 <= y && y <= 402) && (195 <= x && x <= 295)) {
            if (point == 'H') {
                setFan({ ...fan, x: 258, y: 346, correctPoint: 'H' });
            } else if (point == 'I') {
                setTV({ ...tv, x: 244, y: 358, correctPoint: 'H' });
            } else {
                return false;
            }
        }
        return true;
    }

    const handleDragController = (e, ui) => {
        setController({ ...controller, x: controller.x + ui.deltaX, y: controller.y + ui.deltaY });
    }

    const handleControllerStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'A');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragBattery = (e, ui) => {
        setBattery({ ...battery, x: battery.x + ui.deltaX, y: battery.y + ui.deltaY });
    }

    const handleBatteryStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'B');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragInverter = (e, ui) => {
        setInverter({ ...inverter, x: inverter.x + ui.deltaX, y: inverter.y + ui.deltaY });
    }

    const handleInverterStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'C');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragLED = (e, ui) => {
        setLED({ ...led, x: led.x + ui.deltaX, y: led.y + ui.deltaY });
    }

    const handleLEDStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'E');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragCFL = (e, ui) => {
        setCFL({ ...cfl, x: cfl.x + ui.deltaX, y: cfl.y + ui.deltaY });
    }

    const handleCFLStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'F');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragDC = (e, ui) => {
        setDC({ ...dc, x: dc.x + ui.deltaX, y: dc.y + ui.deltaY });
    }

    const handleDCStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'D');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragAC = (e, ui) => {
        setAC({ ...ac, x: ac.x + ui.deltaX, y: ac.y + ui.deltaY });
    }

    const handleACStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'G');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragTV = (e, ui) => {
        setTV({ ...tv, x: tv.x + ui.deltaX, y: tv.y + ui.deltaY });
    }

    const handleTVStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'I');
        if (!isOkay) alert('Wrong point');
    }

    const handleDragFan = (e, ui) => {
        setFan({ ...fan, x: fan.x + ui.deltaX, y: fan.y + ui.deltaY });
    }

    const handleFanStop = (e, position) => {
        const { x, y } = { ...position };
        const isOkay = checkWhichPoint(x, y, 'H');
        if (!isOkay) alert('Wrong point');
    }


    return (
        <div>
            <div style={{ width: '1020px', height: '82%', position: 'absolute', border: 'solid', borderWidth: '0.1rem', borderColor: 'white', left: '10px', bottom: '10px' }} >
                <div style={{ display: 'flex', flexDirection: "row", height: '100%', width: '100%' }}>
                    <div style={{ flex: 19, border: 'solid', borderWidth: '1px', background: 'white',paddingTop:'15px' }} >

                        <div style={{ height: '70px', width: '70px', position: 'absolute', top: `${sunPosition.top}px`, left: `${sunPosition.left}px` }}>
                            <img src={sunSrc} width='70px' height='70px' />
                        </div>
                        <div style={{ height: '70px', width: '80px', position: 'absolute', top: `${sunPosition.top}px`, left: `${sunPosition.left+100}px` }}>
                            <img src={raysSrc} width='80px' height='70px' />
                        </div>
                        <div style={{ height: '80px', width: '80px', position: 'absolute',  top: `${sunPosition.top-5}px`, left: `${sunPosition.left+220}px`}}>
                            <img src={solarSrc} width='80px' height='80px' />
                        </div>

                        <div style={{ position: 'absolute',  top: `${sunPosition.top+25}px`, left: `${sunPosition.left+280}px`, width: '187px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: `${sunPosition.top+35}px`, left: `${sunPosition.left+275}px`, width: '191px', height: '5px', background: 'red' }}>
                        </div>

                        <div style={{ position: 'absolute', top: '115px', left: '602px', width: '5px', height: '95px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '115px', left: '612px', width: '5px', height: '95px', background: 'red' }}>
                        </div>

                        <div style={{ position: 'absolute', top: '270px', left: '602px', width: '5px', height: '100px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '270px', left: '612px', width: '5px', height: '100px', background: 'red' }}>
                        </div>

                        <div style={{ position: 'absolute', top: '235px', left: '497px', width: '85px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '245px', left: '497px', width: '85px', height: '5px', background: 'red' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '235px', left: '306px', width: '98px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '245px', left: '306px', width: '98px', height: '5px', background: 'red' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '235px', left: '150px', width: '132px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '245px', left: '150px', width: '132px', height: '5px', background: 'red' }}>
                        </div>

                        <div style={{ position: 'absolute', top: '400px', left: '478px', width: '104px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '410px', left: '478px', width: '104px', height: '5px', background: 'red' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '400px', left: '150px', width: '115px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '410px', left: '150px', width: '115px', height: '5px', background: 'red' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '400px', left: '320px', width: '90px', height: '5px', background: 'black' }}>
                        </div>
                        <div style={{ position: 'absolute', top: '410px', left: '320px', width: '90px', height: '5px', background: 'red' }}>
                        </div>


                        {/* <div style={{  position: 'absolute', top: '90px', left: '408px' }}>
                            <img src={wire2Src} width='159px' height='22px' />
                        </div> */}
                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '72px', left: '604px' }}>
                            <p>A</p>
                        </div>
                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '235px', left: '604px' }}>
                            <p>B</p>
                        </div>
                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '400px', left: '604px' }}>
                            <p>C</p>
                        </div>


                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '235px', left: '440px' }}>
                            <p>D</p>
                        </div>
                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '235px', left: '290px' }}>
                            <p>E</p>
                        </div>
                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '235px', left: '134px' }}>
                            <p>F</p>
                        </div>

                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '400px', left: '440px' }}>
                            <p>G</p>
                        </div>

                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '400px', left: '290px' }}>
                            <p>H</p>
                        </div>

                        <div style={{ height: '20px', width: '20px', position: 'absolute', top: '400px', left: '134px' }}>
                            <p>I</p>
                        </div>
                        {
                            controller.show && <Draggable
                                bounds="parent"
                                disabled={controller.correctPoint}
                                defaultPosition={{ x: controller.x, y: controller.y }}
                                position={{ x: controller.x, y: controller.y }}
                                onDrag={(e, ui) => handleDragController(e, ui)}
                                onStop={handleControllerStop}
                            >
                                <div style={{
                                    width: '90px', height: '90px', background: `url(${controllerSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            battery.show && <Draggable
                                bounds="parent"
                                disabled={battery.correctPoint}
                                defaultPosition={{ x: battery.x, y: battery.y }}
                                position={{ x: battery.x, y: battery.y }}
                                onDrag={(e, ui) => handleDragBattery(e, ui)}
                                onStop={handleBatteryStop}
                            >
                                <div style={{
                                    width: '90px', height: '90px', background: `url(${batterySrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }
                        {
                            inverter.show && <Draggable
                                bounds="parent"
                                disabled={inverter.correctPoint}
                                defaultPosition={{ x: inverter.x, y: inverter.y }}
                                position={{ x: inverter.x, y: inverter.y }}
                                onDrag={(e, ui) => handleDragInverter(e, ui)}
                                onStop={handleInverterStop}
                            >
                                <div style={{
                                    width: '90px', height: '90px', background: `url(${inverterSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            led.show && <Draggable
                                bounds="parent"
                                disabled={led.correctPoint != ''}
                                defaultPosition={{ x: led.x, y: led.y }}
                                position={{ x: led.x, y: led.y }}
                                onDrag={(e, ui) => handleDragLED(e, ui)}
                                onStop={handleLEDStop}
                            >
                                <div style={{
                                    height: '90px', width: '90px', background: `url(${ledSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            cfl.show && <Draggable
                                bounds="parent"
                                disabled={cfl.correctPoint != ''}
                                defaultPosition={{ x: cfl.x, y: cfl.y }}
                                position={{ x: cfl.x, y: cfl.y }}
                                onDrag={(e, ui) => handleDragCFL(e, ui)}
                                onStop={handleCFLStop}
                            >
                                <div style={{
                                    width: '90px', height: '90px', background: `url(${cflSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            ac.show && <Draggable
                                bounds="parent"
                                disabled={ac.correctPoint}
                                defaultPosition={{ x: ac.x, y: ac.y }}
                                position={{ x: ac.x, y: ac.y }}
                                onDrag={(e, ui) => handleDragAC(e, ui)}
                                onStop={handleACStop}
                            >
                                <div style={{
                                    height: '100px', width: '100px', background: `url(${acSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            dc.show && <Draggable
                                bounds="parent"
                                disabled={dc.correctPoint}
                                defaultPosition={{ x: dc.x, y: dc.y }}
                                position={{ x: dc.x, y: dc.y }}
                                onDrag={(e, ui) => handleDragDC(e, ui)}
                                onStop={handleDCStop}
                            >
                                <div style={{
                                    width: '100px', height: '100px', background: `url(${dcSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            tv.show && <Draggable
                                bounds="parent"
                                disabled={tv.correctPoint != ''}
                                defaultPosition={{ x: tv.x, y: tv.y }}
                                position={{ x: tv.x, y: tv.y }}
                                onDrag={(e, ui) => handleDragTV(e, ui)}
                                onStop={handleTVStop}
                            >
                                <div style={{
                                    width: '100px', height: '90px', background: `url(${tvSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        {
                            fan.show && <Draggable
                                bounds="parent"
                                disabled={fan.correctPoint != ''}
                                defaultPosition={{ x: fan.x, y: fan.y }}
                                position={{ x: fan.x, y: fan.y }}
                                onDrag={(e, ui) => handleDragFan(e, ui)}
                                onStop={handleFanStop}
                            >
                                <div style={{
                                    width: '100px', height: '100px', background: `url(${fanSrc})`, backgroundSize: 'contain',
                                    backgroundRepeat: 'no-repeat', position: 'absolute'
                                }} >
                                </div>
                            </Draggable>
                        }

                        <div style={{ textAlign: 'center' }}>
                            <h3 style={{ marginTop: '0px' }}>Complete the circuit</h3>
                        </div>
                        <div style={{ position: 'absolute', top: '480px', left: '120px' }}>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: controller.correctPoint ? 'green' : '#C41E3A' }}>A</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: battery.correctPoint ? 'green' : '#C41E3A' }}>B</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: inverter.correctPoint ? 'green' : '#C41E3A' }}>C</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: dc.correctPoint ? 'green' : '#C41E3A' }}>D</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: (cfl.correctPoint == 'E' || led.correctPoint == 'E') ? 'green' : '#C41E3A' }}>E</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: (cfl.correctPoint == 'F' || led.correctPoint == 'F') ? 'green' : '#C41E3A' }}>F</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: ac.correctPoint ? 'green' : '#C41E3A' }}>G</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: (tv.correctPoint == 'H' || fan.correctPoint == 'H') ? 'green' : '#C41E3A' }}>H</h4>
                            <h4 style={{ display: 'inline', marginRight: '45px', color: (tv.correctPoint == 'I' || fan.correctPoint == 'I') ? 'green' : '#C41E3A' }}>I</h4>
                        </div>
                        <div style={{ position: 'absolute', top: '470px', right: '280px' }}>
                            <button onClick={() => props.onStepChange(4)} className="button-solarPV" role="button"><i className="fa fa-play" style={{ marginRight: '5px' }} ></i>  Next</button>
                        </div>
                    </div>
                    <div style={{ flex: 6, backgroundColor: '#A9A9A9', paddingTop: '20px' }} >
                        {/* <div>
                            <img src={ledSrc} width='30px' className='margin-left-30px' /><p style={{marginTop:'20px',display:'inline'}}> LED Bulb</p>
                            <img src={batterySrc} width='30px' className='margin-left-30px'/><p style={{paddingBottom:'20px',display:'inline'}}> LED Bulb</p>
                            <img src={controllerSrc} width='30px' className='margin-left-30px' /><p style={{marginTop:'20px',display:'inline'}}> LED Bulb</p>
                            <img src={cflSrc} width='30px' className='margin-left-30px'/><span> LED Bulb</span>
                            <img src={fanSrc} width='30px' className='margin-left-30px'/><span> LED Bulb</span>
                            <img src={inverterSrc} width='30px' className='margin-left-30px' /><span> LED Bulb</span>
                            <img src={dcSrc} width='30px' className='margin-left-30px'/><span> LED Bulb</span>
                            <img src={acSrc} width='30px' className='margin-left-30px' /><span> LED Bulb</span>
                            <img src={tvSrc} height='27px' className='margin-left-30px'/><span> LED Bulb</span>
                        </div> */}
                        <div className="grid-container">
                            <div><img src={ledSrc} height='40px' className='margin-left-30px' onClick={() => setLED({ ...led, show: true })} /></div>
                            <div style={{ marginTop: '10px' }}><span > LED Bulb</span></div>

                            <div><img src={batterySrc} height='30px' className='margin-left-30px' onClick={() => setBattery({ ...battery, show: true })} /></div>
                            <div><span> Battery</span></div>

                            <div><img src={controllerSrc} height='30px' className='margin-left-30px' onClick={() => setController({ ...controller, show: true })} /></div>
                            <div  ><span> Controller</span></div>

                            <div ><img src={cflSrc} height='40px' className='margin-left-30px' onClick={() => setCFL({ ...cfl, show: true })} /></div>
                            <div style={{ marginTop: '2px' }}><span> CFL Bulb</span></div>

                            <div ><img src={fanSrc} height='40px' className='margin-left-30px' onClick={() => setFan({ ...fan, show: true })} /></div>
                            <div ><span> Fan</span></div>

                            <div ><img src={inverterSrc} height='40px' className='margin-left-30px' onClick={() => setInverter({ ...inverter, show: true })} /></div>
                            <div><span> Inverter</span></div>

                            <div ><img src={dcSrc} height='40px' className='margin-left-30px' onClick={() => setDC({ ...dc, show: true })} /></div>
                            <div ><span> DC </span></div>

                            <div ><img src={acSrc} height='40px' className='margin-left-30px' onClick={() => setAC({ ...ac, show: true })} /></div>
                            <div ><span> AC</span></div>

                            <div><img src={tvSrc} height='32px' className='margin-left-30px' onClick={() => setTV({ ...tv, show: true })} /></div>
                            <div ><span> TV</span></div>
                        </div>
                        {/* <button onClick={onAddBulb}>Add Bulb</button> */}
                        {/* <button onClick={() => setController({ ...controller, show: true })} style={{ marginTop: '50px' }}> Controller</button> */}
                        {

                            // <div>{tv.x} , {tv.y}</div>
                        }
                    </div>
                </div>
            </div>
        </div>
    );
}